export default{
	data() {
		return {
			yellowStar:0,
			banStar:0,
			grayStar:0
		}
	
	},
	
	props:{
		score:0,
		showScore:""
	},
	created(){
		var Star = this.score / 2
		this.yellowStar = parseInt(this.score / 2)
		
		if(Star - this.yellowStar>=0.5){
			this.banStar = 1
		}
		
		this.grayStar = 5 - this.yellowStar - this.banStar
	}
}
